<template>
  <div class="catalogue">
    <b-row>
      <b-col class="mb-5 col-md-3">
        <b-list-group>
          <b-list-group-item class="text-white py-2 listt catt">Catalogue</b-list-group-item>
          <b-list-group-item href="#" @click="goRouter('lake')" class="text-nowrap py-2 listt" :class="class_lake">{{name.lake}}</b-list-group-item>
          <b-list-group-item href="#" @click="goRouter('park')" class="text-nowrap py-2 listt" :class="class_park">{{name.park}}</b-list-group-item>
        </b-list-group> 
      </b-col>
      <b-col class="col-md-9">
        <p class="border-bottom pb-2 text-nowrap titt">
          <span @click="goRouter('home')" class="homee">Home</span>
          <span class="namee"> / {{name[router_now.name]}}</span>
        </p>
        <router-view/>
      </b-col>
    </b-row>
  </div>
</template>

<script>
  import { useRouter } from 'vue-router'
  import { reactive, computed } from 'vue'

  export default {
    data() {
      return {
        name: {
          'lake': 'Blackstrap Lake',
          'park': 'Huascaran National Park'
        }
      }
      
    },
    setup() {
      const router = useRouter()
      function goRouter(name) {
        router.push({name})
      }

      let router_now = reactive(router.currentRoute)
      let class_lake = computed(() => (router_now.value.name == 'lake') ? 'lake-park-active' : '')
      let class_park = computed(() => (router_now.value.name == 'park') ? 'lake-park-active' : '')




      return {
        goRouter, class_lake, class_park,router_now
      }
    }
  }
</script>

<style scoped>
  .catalogue {
    padding: calc(5vh + 1vw) 10vw;
  }

  .listt {
    overflow: hidden; 
    text-overflow: ellipsis;
    text-align: center;
    background-color: #e9ecef;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
  }
  .lake-park-active {
    background-color: #fff;
    color: #ff3d00 !important;
  }
  :deep(.list-group-item) {
    border: 0;
  }
  :deep(.list-group-item:hover) {
    color: #ff3d00;
    background-color: #f4f6f8;
  }

  .catt {
    background-color: #ff3d00 !important;
    font-size: 1.75rem;
    letter-spacing: 1px;
    font-weight: 500;
    line-height: 1.2;
  }
  .titt {
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .homee {
    color: #ff3d00;
  }
  .namee {
    color: #6c757d!important;
  }
</style>>