      <p class="text-center mx-auto">We will provide satisfactory services for new and old friends, so that customers get happy and enjoyable travel experience, sincerely thank the support of new and old friends!</p>
      <div class="pt-4 mt-md-4">
        <div class="mt-5">
          <h6>Please fill this form to contact with us.</h6>
          <b-form @submit="onSubmit" @reset="onReset" >
            <b-form-input v-model="form.name" placeholder="Name" required></b-form-input>
            <b-form-input v-model="form.email" placeholder="Email" type="email" required></b-form-input>
            <b-form-input v-model="form.subject" placeholder="Subject" required></b-form-input>
            <b-form-textarea v-model="form.message" placeholder="Message..." rows="3" max-rows="6" required></b-form-textarea>
            <b-button type="submit" class="submitt">SUBMIT</b-button>
            <b-button type="reset" class="mx-1">CLEAR</b-button>
