<script>
import { reactive } from 'vue'
export default {
  name: 'pageContact',
  setup() {
    const form = reactive({
      name: '',
      email: '',
      subject: '',
      message: ''
    })
    function onSubmit() {
      console.log(JSON.stringify(form))
      onReset()
    }
    function onReset() {
      form.name=''
      form.email=''
      form.subject=''
      form.message=''
    }
    return {
      form, onSubmit, onReset
    }
  }
}
</script>

