<template>
  <b-row cols-lg="3" cols-md="2" cols="1">
    <b-col v-for="image in images" :key="image" class="px-0">
      <div class="mx-1 my-1 col_img">
        <img :src="require(`../assets/images/HuascaranNationalPark${image}-small.jpg`)" @click="showImg(image)"/>
      </div>
    </b-col>
  </b-row>
  <!-- <div>
    <transition-group name="all">
      <div v-for="image in images" :key="image" v-show="shown[image - 1]" class="position-fixed img_cover" @click="hideImg(image)">
          <div class="px-2 py-2 position-relative bacc">
            <div @click="lastImg(image)" class="position-absolute arroww_l" @mouseenter="seenl=true" @mouseleave="seenl=false">
              <transition name="arroww">
                <div class="arrow_l" v-show="seenl"></div>
              </transition>
            </div>
            <div @click="nextImg(image)" class="position-absolute arroww_r" @mouseenter="seenr=true" @mouseleave="seenr=false">
              <transition name="arroww">
                <div class="arrow_r" v-show="seenr"></div>
              </transition>
            </div>
            <b-img :src="require(`../assets/images/HuascaranNationalPark${image}.jpg`)" class="raw_img"/>
          </div>
        <p class="img_context">Image {{image}} of 6</p>
      </div>
    </transition-group>
  </div> -->
  <transition name="greyy">
    <div class="position-fixed img_cover" @click="hideImg()" v-show="shown_grey">
      <div @click.stop="" class="position-relative bacc" :style="{height: height_now + 'px', width: width_now + 'px'}">
        <div @click.stop="lastImg()" class="position-absolute arroww_l" @mouseenter="seenl=true" @mouseleave="seenl=false">
          <transition name="arroww">
            <div class="arrow_l" v-show="seenl"></div>
          </transition>
        </div>
        <div @click.stop="nextImg()" class="position-absolute arroww_r" @mouseenter="seenr=true" @mouseleave="seenr=false">
          <transition name="arroww">
            <div class="arrow_r" v-show="seenr"></div>
          </transition>
        </div>
        <transition-group name="imggg">
          <div v-for="image in images" :key="image" v-show="shown[image - 1]">
            <b-img :src="require(`../assets/images/HuascaranNationalPark${image}.jpg`)" class="raw_img" ref="imges"/>
            <p class="position-absolute img_context">Image {{image}} of 11</p>
          </div>
        </transition-group>
      </div>
    </div>
  </transition>
</template>

<script>
  import { ref, reactive } from 'vue'
  export default {
    name: 'pagePark',
    data() {
      return {
        images: [1, 2, 3, 4, 5, 6]
      }
    },
    setup() {
      const shown = reactive([false, false, false, false, false, false])
      const shown_index = ref(-1)
      const shown_grey = ref(false)
      function showImg(index) {
        height_now.value = imges.value[index - 1].$el.height + 20
        width_now.value = imges.value[index - 1].$el.width + 20
        shown_grey.value = true
        shown[index - 1] = true
        shown_index.value = index - 1
      }
      function hideImg() {
        shown[shown_index.value] = false
        shown_index.value = -1
        shown_grey.value = false
        height_now.value = 0
        width_now.value = 0
      }
      function nextImg() {
        shown[shown_index.value] = false
        if (shown_index.value == 5) shown_index.value = 0
        else shown_index.value++
        setTimeout(() => shown[shown_index.value] = true, 50)
        height_now.value = imges.value[shown_index.value].$el.height + 20
        width_now.value = imges.value[shown_index.value].$el.width + 20
      }
      function lastImg() {
        shown[shown_index.value] = false
        if (shown_index.value == 0) shown_index.value = 5
        else shown_index.value--
        setTimeout(() => shown[shown_index.value] = true, 50)
        height_now.value = imges.value[shown_index.value].$el.height + 20
        width_now.value = imges.value[shown_index.value].$el.width + 20
      }

      let seenl = ref(false)
      let seenr = ref(false)

      const imges = ref(null)
      const height_now = ref(0)
      const width_now = ref(0)

      return {
        shown, showImg, hideImg, nextImg, lastImg, seenl, seenr, shown_grey, imges, height_now, width_now
      }
    }
  }
</script>

<style scoped>
  .col_img img {
    width: 100%;
    transition: transform .75s;
    transition-timing-function: ease-out;
  }
  .col_img img:hover {
    transform: scale(1.2);
  }
  .col_img {
    overflow: hidden;
    border-radius: 7px;
  }

  .raw_img {
    max-height: 100%;
    max-width: 100%;
    border-radius: 5px;
  }
  .img_cover {
    background-color: rgba(0, 0, 0, 0.8);
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .img_context {
    color: #dee2e6;
    padding: 0.5em 0;
    line-height: 1.5;
    font-size: 1rem;
  }
  .arroww_l {
    left: 0;
    width: 50%;
    height: 100%;
  }
  .arrow_l {
    background: url(../assets/images/prev.png) no-repeat left;
    width: 100%;
    height: 100%
  }
  .arroww_r {
    width: 50%;
    height: 100%;
    right: 0;
  }
  .arrow_r {
    background: url(../assets/images/next.png) no-repeat right;
    width: 100%;
    height: 100%;
  }
  .bacc {
    background: white;
    border-radius: 5px;
    text-align: center;
    margin: auto;
    transition: all 0.5s ease;
    padding: 10px;
  }

  .arroww-enter-active,
  .arroww-leave-active {
    transition: all 0.5s ease;
  }
  .arroww-enter-from,
  .arroww-leave-to {
    opacity: 0;
  }

  .greyy-enter-active, 
  .greyy-leave-active {
    transition: all 0.4s ease;
  }
  .greyy-enter-from, 
  .greyy-leave-to {
    opacity: 0;
  }

  .imggg-enter-active{
    transition: all 0.5s ease 0.6s;
  }
  .imggg-leave-active {
    transition: all 0.1s ease;
  }
  .imggg-enter-from,
  .imggg-leave-to {
    opacity: 0;
  }
</style>