<template>
  <div :class="all">

    <b-navbar toggleable="lg" type="light">
      <b-navbar-brand @click="goRouter('home')">
        <img src="../assets/images/logo.png" />
      </b-navbar-brand>
      <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>
      <b-collapse id="nav-collapse" is-nav class="navv_item">
        <b-navbar-nav>
          <b-nav-item v-for="(tag, index) in tags" :key="index" @click="goRouter(tag)" :active="tag==router_now">{{tag}}</b-nav-item>
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>

    <div class="typer" v-if="router_now=='home'">
      <!-- <div v-if="sentence_shown == 0">We Provide Best Guidance</div>
      <div v-else>Experience The Best Trip</div> -->
      <div>{{shown}}</div>
      <p class="mx-auto py-3">Specialize in providing satisfactory travel services and outdoor experiences for customers who wish to travel outdoors.</p>
    </div>
      
  </div>
</template>

<script>
import { useRouter } from "vue-router"
import { ref, computed, watch } from "vue"
// import { onMounted, onUnmounted } from "vue"

export default {
  name: 'navBar',
  // props: {
  //   isHome: {
  //     type: Boolean,
  //     dafault: false,
  //   },
  // },
  data () {
    return {
      tags: ['home', 'about', 'contact'],
    }
  },
  setup () {

    
    let letter_shown = ref(1)
    let sentence_shown = ref(0)
    const sentence = ['We Provide Best Guidance', 'Experience The Best Trip']
    let shown = computed( () => sentence[sentence_shown.value].slice(0, letter_shown.value) )
    let interval = ref('')

    const router = useRouter()
    function goRouter (name) {
        router.push({name})
    }
    const router_n = router.currentRoute
    const router_now = computed(() => router_n.value.name)

    const all = computed(() => (router_now.value == 'home') ? 'all_home' : 'all_others')

    function typing () {
      if (letter_shown.value == 50) {
        letter_shown.value = 1
        sentence_shown.value = (sentence_shown.value == 1) ? 0 : 1
      }
      letter_shown.value += 1
      // console.log(letter_shown.value, sentence_shown.value)
    }

    // onMounted( () => {
    //   interval = setInterval( () => {typing()}, 60 )
    // })
    // onBeforeUnmount( () => {
    //   clearInterval(interval)
    //   // clearTimeout(timeout)
    // })
    
    watch(router_now, (value) => {
      if (value == 'home') {
        interval = setInterval( () => { typing() }, 100 )
      } else {
        if (interval.value) clearInterval(interval)
      }
    })

    return {
      all,
      router_now,
      shown,
      goRouter
    }
  }
}
</script>

<style scoped>
  img {
    width: 100px;
    margin-left: 10vw;
  }
  .all_home {
    background: url(../assets/images/bg.jpg)no-repeat 0px 0px;
    background-attachment: fixed;
    min-height: calc(400px + 15vw);
    color: #fff;
  }
  .all_others {
    background: url(../assets/images/bg.jpg)no-repeat 0px -90px;
    background-attachment: fixed;
    min-height: calc(200px + 10vw);
  }
  .nav-item :deep(a) {
    color: #fff;
    transition: 0.5s all;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 600;
    letter-spacing: 0.1em;
    text-align: right;
  }
  .navv_item{
    justify-content: end;

  }
  ul {
    padding-right: 5vw;
  }
  nav {
    background-color: rgba(0, 0, 0, 0.2);
  }
  .nav-item :deep(.nav-link.active),
  .nav-item :deep(a:hover) {
      color: #ff3d00;
      transition: 0.5s all;
  }
  .typer {
    padding: 10vw 0 0;
    text-align: center;
    font-size: 3em;
    letter-spacing: 3px;
    text-transform: uppercase;
    font-family: 'Roboto Condensed', sans-serif;
  }
  .typer p {
    width: calc(50vw + 30vh);
    max-width: 100%;
    font-size: 16px;
    letter-spacing: 2px;
    line-height: 30px;
    text-align: center;
  }

</style>