    // router
    const router = useRouter()
    function goRouter(name) {
      router.push({ name })
    }

    //team img show name
    const shown = reactive([false, false, false, false])
    const showName = debounce(sn)
    // const hideName = debounce(hn)
    function hideName(index) {
      setTimeout(() => shown[index] = false, 300)
    }
    function sn(index) {
      shown[index] = true
    }
    // function hn(index) {
    //   shown[index] = false
    // }
    function debounce(fn) {
      let timeout = null
      return function() {
        let self = this
        if (timeout) {
          clearTimeout(timeout)
        }
        timeout = setTimeout(() => fn.apply(self, arguments), 300)
      }
    }

    return {
      goRouter,
      shown,
      showName,
      hideName,

    }
  }
}
