<template>
  <img :src="require('../assets/images/arrow.png')" @click="goToTop()" v-show="totopshow"/>

</template>

<script>
  import { ref, onMounted, onBeforeUnmount } from 'vue'
  export default {
    name: 'toTop',
    setup(){
      function goToTop() {
        window.scrollTo(0, 0)
      }

      let totopshow = ref(false)
      onMounted(() => window.addEventListener('scroll', listenScroll, true))
      onBeforeUnmount(() => window.removeEventListener('scroll', listenScroll, true))
      function listenScroll(e) {
        if (e.path[1].scrollY > window.innerHeight) totopshow.value = true
        else totopshow.value = false
      }

      return {
        goToTop, totopshow
      }
    }
  }
</script>

<style scoped>
  img {
    position: fixed;
    right: 50px;
    bottom: 50px;
  }
</style>