
import { createApp } from 'vue'
import App from './App.vue'
import router from './router/index.js'
import BootstrapVue3 from 'bootstrap-vue-3'

import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import { faFacebookF, faTwitter, faGooglePlusG, faPinterestP } from '@fortawesome/free-brands-svg-icons'
import { faBus, faAnchor, faHome, faFighterJet } from '@fortawesome/free-solid-svg-icons'
library.add(faFacebookF, faTwitter, faGooglePlusG, faPinterestP, faBus, faAnchor, faHome, faFighterJet)

const app = createApp(App)
app.use(router)
app.use(BootstrapVue3)
app.component('font-awesome-icon', FontAwesomeIcon)
app.mount('#app')

